import React, { useEffect, useState } from 'react';
import { useRouter } from 'next/router';
import { ICON_NAMES, SVGIcon } from '@react-page/editor';
import type { NextPage } from 'next';
import OtpInput from 'react-otp-input';
import { toast } from 'react-toastify';
import { ButtonLoading } from '@react-page/editor';
import { ActionType, useStateContext } from '../state';

type verifyModalParamType = {
  email: string;
  setIsVerifyPage: (value: boolean) => void;
};

const VerificationModal: NextPage<verifyModalParamType> = ({
  email,
  setIsVerifyPage,
}) => {
  const {
    state: { user, token },
    dispatch,
  } = useStateContext();
  const router = useRouter();
  const [code, setCode] = useState('');
  const [loading, setLoading] = useState(false);

  const resend = async () => {
    const options = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ email: email }),
    };

    try {
      await fetch(
        `${process.env.NEXT_PUBLIC_SERVER_API_URL}/auth/resend`,
        options
      )
        .then((res) => res.json())
        .then((res) => {
          if (res.code === 200) {
            // setIsVerifyPage(true);
            toast.success('Resend Success!');
          } else {
            toast.error(res.message);
          }
          setLoading(false);
        });
    } catch (error) {
      toast.error('Network Error!');
      setLoading(false);
    }
  };

  useEffect(() => {
    if (code.length === 6) {
      verification();
    }
  }, [code]);

  const verification = async () => {
    setLoading(true);
    const options = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ email: email, verificationCode: code }),
    };

    try {
      await fetch(
        `${process.env.NEXT_PUBLIC_SERVER_API_URL}/auth/verify`,
        options
      )
        .then((res) => res.json())
        .then((res) => {
          if (res.code === 200) {
            dispatch({
              type: ActionType.UPDATE_USER,
              payload: res.data,
            });
            dispatch({
              type: ActionType.UPDATE_TOKEN,
              payload: res.data.token,
            });
            router.push('/home');
          } else {
            toast.error(res.message);
          }
          setLoading(false);
        });
    } catch (error) {
      toast.error('Network Error!');
      setLoading(false);
    }
  };

  const go_head = () => {
    setIsVerifyPage(false);
  };

  return (
    <div className="flex w-full items-center justify-center h-[100vh]">
      <div className="flex flex-col w-[400px] h-fit bg-alpha-10 rounded-[15px] p-[30px] gap-[20px]">
        <div className="flex items-center justify-center w-full">
          <img
            className="w-[130px] shrink-0 object-cover"
            src="/images/mintchip-beta-logo.png"
          />
        </div>
        <div>
          <div className="text-gray-2 text-[18px] font-bold">
            Check your email
          </div>
          <div className="text-gray-1 text-[16px]">to continue to MINTCHIP</div>
        </div>
        <div className="flex flex-row items-center gap-[10px] w-fit rounded-full h-[40px] px-[16px] py-[6px] border-gray-1 border-[1px] border-solid">
          <img
            className="rounded-full h-full"
            src="https://www.gravatar.com/avatar?d=mp"
            alt=""
          />
          <div className="text-gray-1 text-[14px]">{email}</div>
          <div
            onClick={() => go_head()}
            className="flex items-center justify-center hover:cursor-pointer"
          >
            <SVGIcon name={ICON_NAMES.EDIT} />
          </div>
        </div>
        <div>
          <div className="flex flex-row items-center justify-start text-gray-2 text-[14px]">
            Verification code
          </div>
          <div className="flex flex-row items-center justify-start text-gray-1 text-[14px]">
            Enter the verification code sent to your email address
          </div>
        </div>
        <div className="flex flex-row items-center justify-start gap-[20px]">
          <OtpInput
            value={code}
            onChange={setCode}
            numInputs={6}
            isInputNum={true}
            containerStyle="flex flex-row gap-[10px]"
            inputStyle="border-b-[2px] text-[26px] text-white text-center border-solid border-gray-1 bg-transparent hidden-arrow focus:outline-none focus:border-[#0048DB]"
          />
          {loading === true && <ButtonLoading />}
        </div>
        <div
          onClick={() => resend()}
          className="flex flex-row items-center justify-start text-primary-blue text-[14px] hover:cursor-pointer hover:underline"
        >
          Resend code
        </div>
      </div>
    </div>
  );
};

export default VerificationModal;
